/* Global styles, always imported */
html {
  font-size: 16px; }

body {
  color: #303133;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

a {
  text-decoration: none;
  color: #409EFF;
  transition: color 0.2s ease-in-out; }
  a:visited {
    color: #409EFF; }
  a:hover {
    color: #006bd9; }

.container {
  max-width: 1200px;
  margin: 0 auto; }

.page-container {
  min-height: 100vh;
  margin-top: 2rem; }

.page-title {
  font-size: 2rem;
  margin-bottom: 2em;
  font-weight: 600; }

.error-label {
  color: #F56C6C;
  opacity: 0;
  font-size: 0.8rem; }
  .error-label--show {
    opacity: 1; }
