/* Global styles, always imported */
html {
  font-size: 16px; }

body {
  color: #303133;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

a {
  text-decoration: none;
  color: #409EFF;
  transition: color 0.2s ease-in-out; }
  a:visited {
    color: #409EFF; }
  a:hover {
    color: #006bd9; }

.container {
  max-width: 1200px;
  margin: 0 auto; }

.page-container {
  min-height: 100vh;
  margin-top: 2rem; }

.page-title {
  font-size: 2rem;
  margin-bottom: 2em;
  font-weight: 600; }

.error-label {
  color: #F56C6C;
  opacity: 0;
  font-size: 0.8rem; }
  .error-label--show {
    opacity: 1; }

.nav-logo {
  display: inline-block;
  color: #909399;
  font-weight: 600;
  display: inline-block;
  margin-right: 2em; }

.top-bar {
  background-color: #00060d; }
  .top-bar__container {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center; }

.main-menu {
  padding: 1em 0.5em; }
  .main-menu__list {
    display: -webkit-flex;
    display: flex;
    list-style: none;
    margin: 0; }
  .main-menu__element {
    padding: 0 1em; }

.footer {
  background-color: #00060d;
  padding: 0.8rem 0; }
  .footer__content {
    color: #909399;
    text-align: center; }

.new-company__newinfo {
  color: #909399;
  margin-top: 0.3em;
  font-size: 0.8rem; }

.new-company__track-btn {
  margin-top: 60px; }

.new-company__error {
  margin-top: 25px;
  transition: opacity 0.5s ease-in-out; }

.company {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  font-size: 0.9rem;
  margin-bottom: 1.5em;
  position: relative;
  max-width: 500px;
  padding: 0.5em;
  background-color: #f3f9ff;
  transition: background-color 0.5s ease-in-out; }
  .company:hover {
    background-color: #d9ecff; }
    .company:hover .company__remove {
      opacity: 1; }
  .company__remove {
    cursor: pointer;
    opacity: 0;
    position: absolute;
    right: 0.5em;
    top: 0.5em;
    color: #F56C6C;
    font-weight: 600;
    transition: opacity 0.5s ease-in-out; }
  .company__line {
    margin-top: 0.3em; }
    .company__line:first-child {
      margin-top: 0; }
  .company__image-container {
    width: 64px;
    height: 64px;
    background-color: #dedede;
    margin-right: 1.2em;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center; }
  .company__image {
    width: 64px;
    height: auto; }
  .company__name {
    font-weight: 600;
    font-size: 1.2em;
    margin-right: 1em; }
  .company__symbol {
    margin-right: 0.8em; }
  .company__open {
    margin-left: 0.8em; }
  .company__timezone {
    margin-left: 0.2em; }
  .company__price {
    font-weight: 600; }
  .company__currency {
    margin-left: 0.2em; }
  .company__change {
    margin-left: 0.8em; }
    .company__change--positive {
      color: #67C23A; }
    .company__change--negative {
      color: #F56C6C; }
  .company__change-percent {
    margin-left: 0.2em; }
    .company__change-percent--positive {
      color: #67C23A; }
    .company__change-percent--negative {
      color: #F56C6C; }
  .company__closed {
    margin-left: 0.8em; }

.input {
  width: 220px;
  height: 25px;
  padding: 0.4em 0.6em;
  color: #303133;
  border-radius: 4px;
  box-shadow: none;
  border: solid 1px #C0C4CC; }
  .input::-webkit-input-placeholder {
    color: #C0C4CC; }
  .input::-ms-input-placeholder {
    color: #C0C4CC; }
  .input::placeholder {
    color: #C0C4CC; }
  .input__label {
    margin-bottom: 0.3em;
    display: block; }

.button {
  font-weight: 600;
  color: #fff;
  background-color: #409EFF;
  transition: background-color 0.5s ease-in-out;
  font-size: 16px;
  border: none;
  cursor: pointer;
  padding: 0.5em 1.5em;
  border-radius: 4px; }
  .button:hover {
    background-color: #0077f3; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none; }

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  color: white; }

.App-link {
  color: #61dafb; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

